.PlutoStyle {
    max-width: calc(700px + 25px + 6px);
    margin-left: auto;
    margin-right: auto;
    padding: 0 1em;

    h1, h2 {
        padding-bottom: 10px;
    }
    h1 {
        font-size: 2.2rem;
        border-bottom: 3px solid rgba(0, 0, 0, 0.15);
        margin-bottom: 0.5rem;
    }
    h2 {
        font-size: 1.8rem;
        border-bottom: 2px dotted rgba(0, 0, 0, 0.15);
        margin-bottom: 0.5rem;
    }
    a {
        color: black;
        font-weight: bold;
    }
    button {
        font-size: 1em;
        border-radius: 5px;
        outline: none;
        border: none;
        padding: 15px;
        cursor: pointer;
        background-color: rgba(0, 0, 0, 0.1);
        transition: background-color 0.2s;

        &:hover {
            background-color: rgba(0, 0, 0, 0.12);
        }
        &:active {
            background-color: rgba(0, 0, 0, 0.15);
        }
    }
}
