.Index-root {
    .notebook-upload-label {
        display: flex;
        justify-content: center;
        border: 2px dashed grey;
        border-radius: 15px;
        padding: 40px;
        font-size: 1.2em;
        cursor: pointer;
    }
    .notebook-placeholder {
        display: flex;
        border: 2px solid lightgray;
        border-radius: 15px;
        padding: 20px 30px;

        .notebook-title {
            display: flex;
            flex-direction: column;

            input {
                cursor: pointer;
                font-style: italic;
                font-size: 1em;
                border: none;
            }
            input:focus {
                font-style: normal;
                cursor: inherit;
            }
        }

        h3 {
            font-weight: normal;
            font-size: 1.2em;
            margin: 0;
        }
        a {
            margin: auto 0;
            cursor: pointer;
        }
        button.upload-button {
            background-color: rgba(0, 78, 255, 1);
            color: white;
            margin-right: 20px;

            &:hover {
                background-color: #0046e5;
            }
            &:active {
                background-color: rgba(0, 7, 225, 1);
            }
        }
    }
}

.Notebook {
    border: 1px solid lightgray;
    border-radius: 15px;
    padding: 8px 20px;
    margin-bottom: 10px;
    display: flex;

    h4 {
        font-weight: normal;
        font-size: 1.1em;
        margin: 0;
    }
    span {
        font-style: italic;
    }
    a {
        text-decoration: none;
        font-weight: inherit;
    }
}